import Vue from 'vue';
import axios from 'axios';
import VueAxios from 'vue-axios';
import 'bootstrap';
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.min.css';
import App from './App.vue';
import router from './router';
import store from './store';
import './bus';
import currencyfilter from './filter/currency'
import VeeValidate from 'vee-validate';
import zhTW_Validate from 'vee-validate/dist/locale/zh_TW';


Vue.config.productionTip = false
Vue.use(VueAxios, axios)
///廣域宣告component,所有的component都可以使用
Vue.component('loading',Loading);
Vue.filter('currency',currencyfilter);
VeeValidate.Validator.localize('zh_TW', zhTW_Validate);
Vue.use(VeeValidate);



//若使用api登入方式為admin需要打開這段，會將cookie存至前端應用程式中
//axios.defaults.withCredentials = true;

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')


//頁面若需要驗證則啟用api check function
router.beforeEach((to, from, next) => {
  console.log('to',to,'from',from,'next',next)
  if(to.meta.requiresAuth){
    console.log('這裡需要驗證');
    const api = `${process.env.VUE_APP_APIMAIN}/api/user/check`;
            console.log(api);
            const vm = this;
            axios.post(api).then((response) => {
            console.log(response.data)
            if(response.data.success){
              next();
            }
            else{
              next({
                  path:'/login'
              })
            }
            });
  }
  else{
    next();
  }
})