<template>
  <div>
      <alert/>
      <form class="form-signin" @submit.prevent="signin">
      <h1 class="h3 mb-3 font-weight-normal">請先登入</h1>
      <label for="inputEmail" class="sr-only">Email address</label>
      <input type="email" id="inputEmail" class="form-control" placeholder="Email address" required v-model="user.username">
      <label for="inputPassword" class="sr-only">Password</label>
      <input type="password" id="inputPassword" class="form-control" placeholder="Password" required v-model="user.password"> 
      <div class="checkbox mb-3">
        <label>
          <input type="checkbox" value="remember-me"> Remember me
        </label>
      </div>
      <button class="btn btn-lg btn-primary btn-block" type="submit">Sign in</button>
      <p class="mt-5 mb-3 text-muted">&copy; 2017-2018</p>
    </form>
  </div>
</template>

<script>
import alert from '../alertmessage';
export default {
    components:{
      alert
    },
    data() {
        return {
            user:{
                username:'',
                password:'',
            }
        }
    },
    methods:{
        signin(){
            const api = `${process.env.VUE_APP_APIMAIN}/signin`;
            //下方註解為使用存取cookie之登入方式
            //const api =`${process.env.VUE_APP_APIMAIN}/admin/signin`;
            console.log(api);
            const vm = this;
            this.$http.post(api,vm.user).then((response) => {
            console.log(response.data)
            if(response.data.success){
                vm.$router.push('/admin/products');
                //若登入成功則轉至首頁
            }
            else{
              this.$bus.$emit("message:push",response.data.message,"danger")
            }
            });
        },
    }
}
</script>

