<template>
    <div>
        <div class="container">
            <div class="row justify-content-center my-3">
                    <h2>訂 單 資 訊</h2>
            </div>
        </div>
        <div class="my-5 row justify-content-center">
            <form class="col-md-6" @submit.prevent="payorder">
                <table class="table">
                <thead>
                    <th>品名</th>
                    <th>數量</th>
                    <th>單價</th>
                </thead>
                <tbody>
                    <tr v-for="item in order.products" :key="item.id">
                    <td class="align-middle">{{ item.product.title }}</td>
                    <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                    <td class="align-middle text-right">{{ item.final_total }}</td>
                    </tr>
                </tbody>
                <tfoot>
                    <tr>
                    <td colspan="2" class="text-right">總計</td>
                    <td class="text-right">{{ order.total }}</td>
                    </tr>
                </tfoot>
                </table>

                <table class="table">
                <tbody>
                    <tr>
                    <th width="100">Email</th>
                    <td>{{ order.user.email }}</td>
                    </tr>
                    <tr>
                    <th>姓名</th>
                    <td>{{ order.user.name }}</td>
                    </tr>
                    <tr>
                    <th>收件人電話</th>
                    <td>{{ order.user.tel }}</td>
                    </tr>
                    <tr>
                    <th>收件人地址</th>
                    <td>{{ order.user.address }}</td>
                    </tr>
                    <tr>
                    <th>付款狀態</th>
                    <td>
                        <span v-if="!order.is_paid">尚未付款</span>
                        <span v-else class="text-success">付款完成</span>
                    </td>
                    </tr>
                </tbody>
                </table>
                <div class="text-right" v-if="order.is_paid === false">
                <button class="btn btn-danger">確認付款去</button>
                </div>
            </form>
            </div>
            <div class="container">
                <div class="row justify-content-center my-3">
                    <div class="col-md-8 text-center">
                        <router-link to="/customorder" class="btn btn-primary">返回繼續購物</router-link>
                    </div>
                </div>
            </div>
    </div>
</template>

<script>
    export default{
        data(){
            return{
                order:{
                    user:{},
                },
                orderId:'',
            }
        },
        methods:{
            getorder(){
                const vm = this;
                const api = `${process.env.VUE_APP_APIMAIN}/api/${process.env.VUE_APP_CUSTOMPATH}/order/${vm.orderId}`;
                console.log(api);
                this.$http.get(api).then(response => {
                    console.log(response.data);
                    vm.order = response.data.order;
                }); 
            },
            payorder(){
                const vm = this;
                const api = `${process.env.VUE_APP_APIMAIN}/api/${process.env.VUE_APP_CUSTOMPATH}/pay/${vm.orderId}`;
                console.log(api);
                this.$http.post(api).then(response => {
                    console.log(response.data);
                    if(response.data.success){
                        vm.getorder();
                    }
                }); 
            }
        },
        created(){
            this.orderId = this.$route.params.orderId;
            this.getorder();
            console.log(this.orderId);
        }   
    }
</script>