import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Login from '@/components/pages/Login.vue'
import Logout from '@/components/pages/Logout.vue'
import Dashboard from '@/components/dashboard.vue'
import Products from '@/components/pages/products.vue'
import customorder from '@/components/pages/order.vue'
import customercheckout from '@/components/pages/customercheckout.vue'
import shoppingcartpage from '@/components/pages/shoppingcart.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      ///若網址出現不存在頁面則跳轉自動轉址
      path:'*',
      redirect:'/login',
    },
    {
      path: '/',
      name: 'home',
      component:Home,
      children:[
        {
          path:'customorder',
          name:'customorder',
          component:customorder,
      },
      {
        path:'customercheckout/:orderId',
        name:'customercheckout',
        component:customercheckout,
      },
      {
        path:'shoppingcartpage',
        name:'shoppingcartpage',
        component:shoppingcartpage,
      },
      ]
    },
    {
      path:'/login',
      name:'login',
      component:Login,
  },
  {
      path:'/logout',
      name:'logout',
      component:Logout,
  },
  {
    path: '/admin',
    name: 'dashboard',
    component:Dashboard,
    children:[
      {
        path:'products',
        name:'products',
        component:Products,
        meta: { requiresAuth: true },
    },
    ]
  },
  
  // {
    //   path: '/about',
    //   name: 'about',
    //   // route level code-splitting
    //   // this generates a separate chunk (about.[hash].js) for this route
    //   // which is lazy-loaded when the route is visited.
    //   component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    // },
  ]
})
