<template>
    <div>
        <!----loading效果---->
        <loading :active.sync="isLoading" :is-full-page="fullPage"></loading>
        <div class="container">
             <div class="row mt-4">
                 <div class="col-md-2 mb-2">
                     <div class="list-group sticky-top">
                        <a class="list-group-item list-group-item-action" data-toggle="list" href="#list-gold" :class="{'active' : productcategory == 'all' }" @click="productcategory = 'all'">
                            <i class="fa fa-suitcase" aria-hidden="true"></i> 全部商品</a>
                        <a class="list-group-item list-group-item-action" data-toggle="list" href="#list-gift" :class="{'active' : productcategory == 'milc' }" @click="productcategory = 'milc'">
                            <i class="fas fa-camera" aria-hidden="true"></i> 微單眼</a>
                        <a href="#" class="list-group-item list-group-item-action" data-toggle="list" :class="{'active' : productcategory == 'slrlike' }" @click="productcategory = 'slrlike'">
                            <i class="fas fa-camera-retro" aria-hidden="true"></i> 類單眼</a>
                        <a href="#" class="list-group-item list-group-item-action" data-toggle="list" :class="{'active' : productcategory == 'dslr' }" @click="productcategory = 'dslr'">
                            <i class="fas fa-video" aria-hidden="true"></i> 專業單眼</a>
                    </div>
                 </div>
                 <div class="col-md-10">
                     <div class="row">
                        <div class="col-md-4 mb-4" v-for="item in filterproduct" :key="item.id">
                            <div class="card border-0 shadow-sm">
                                <div style="height: 150px; background-size: cover; background-position: center" :style="{backgroundImage:`url(${item.imageUrl})`}">
                                </div>
                                <div class="card-body">
                                <span class="badge badge-secondary float-right ml-2">{{ item.category }}</span>
                                <h5 class="card-title">
                                    <a href="#" class="text-dark">{{ item.title }}</a>
                                </h5>
                                <p class="card-text">{{ item.content }}</p>
                                <div class="d-flex justify-content-between align-items-baseline">
                                    <!-- <div class="h5">2,800 元</div> -->
                                    <del class="h6">原價 {{ item.origin_price }} 元</del>
                                    <div class="h5">現在只要 {{ item.price }} 元</div>
                                </div>
                                </div>
                                <div class="card-footer d-flex">
                                <button type="button" class="btn btn-outline-secondary btn-sm" @click="getsingleproduct(item.id)">
                                    <i class="fas fa-spinner fa-spin" v-if=" status.loadingitem === item.id"></i>
                                    查看更多
                                </button>
                                <button type="button" class="btn btn-outline-danger btn-sm ml-auto" @click="addingtocart(item.id)">
                                    <i class="fas fa-spinner fa-spin" v-if=" status.shoppingcart === item.id"></i>
                                    加到購物車
                                </button>
                                </div>
                            </div>
                        </div>
                     </div>
                 </div>
            </div>
        </div>  

            <!-- Modal -->
            <div class="modal fade" id="singleproductModal" tabindex="-1" role="dialog" aria-labelledby="singleproductModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="singleproductModalLabel">{{ product.title }}</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div class="card border-0 shadow-sm">
                                    <div style="height: 300px; background-size: cover; background-position: center center" :style="{backgroundImage:`url(${product.imageUrl})`}">
                                    </div>
                                <div class="card-body">
                                    <span class="badge badge-secondary float-right ml-2">{{ product.category }}</span>
                                    <h5 class="card-title">
                                        <a href="#" class="text-dark">{{ product.title }}</a>
                                    </h5>
                                    <p class="card-text">{{ product.content }}</p>
                                    <div class="d-flex justify-content-between align-items-baseline">
                                        <!-- <div class="h5">2,800 元</div> -->
                                        <del class="h6">原價{{ product.origin_price }}元</del>
                                        <div class="h5">現在只要 {{ product.price }} 元</div>
                                    </div>
                                    <select name="" class="form-control mt-3" v-model="product.num">
                                        <option :value="num" v-for="num in 10" :key="num">選購{{ num }} {{ product.unit }}</option>
                                    </select>
                                </div>
                     </div>
                </div>
                <div class="modal-footer">
                        <button type="button" class="btn btn-outline-danger btn-sm ml-auto" @click="addingtocart(product.id,product.num)">
                            加到購物車</button>
                </div>
                </div>
            </div>
            </div>
    </div>
</template>



<script>
import $ from "jquery";

export default {
  data() {
    return {
      carts:{},
      product:{},
      products: [],
      isnew: false,
      isLoading: false,
      fullPage: true,
      status: {
        loadingitem:'',
        shoppingcart:'',
      },
      pagination: {},
      productcategory:'all',
    };
  },
  methods: {
    getProducts() {
      const api = `${process.env.VUE_APP_APIMAIN}/api/${process.env.VUE_APP_CUSTOMPATH}/products`;
      const vm = this;
      console.log(api);
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        console.log(response.data);
        vm.isLoading = false;
        vm.products = response.data.products;
        vm.pagination = response.data.pagination;
      });
    },
    getsingleproduct(id) {
      const api = `${process.env.VUE_APP_APIMAIN}/api/${process.env.VUE_APP_CUSTOMPATH}/product/${id}`;
      const vm = this;
      console.log(api);
      vm.status.loadingitem = id;
      this.$http.get(api).then(response => {
        $("#singleproductModal").modal("show");
        vm.product = response.data.product;
        console.log(response.data);
        vm.status.loadingitem = '';
      });
    },
    addingtocart(id,qty = 1){
        const api = `${process.env.VUE_APP_APIMAIN}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
        const vm = this;
        const cart = {
            product_id : id,
            qty,
        };
        console.log(api);
        vm.status.shoppingcart = id;
        this.$http.post(api,{data:cart}).then(response => {
            console.log(response.data);
            vm.status.shoppingcart = '';
            if(response.data.success){
                $("#singleproductModal").modal("hide");
            }
            vm.getcart();
        });
    },
    getcart(){
        const api = `${process.env.VUE_APP_APIMAIN}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
        const vm = this;
        console.log(api);
        this.$http.get(api).then(response => {
            vm.carts = response.data.data;
            // vm.finaltotal = response.data.final_total;
            // vm.total = response.data.total;
            console.log(response.data);
        });
    },
    deletecart(id){
        const api = `${process.env.VUE_APP_APIMAIN}/api/${process.env.VUE_APP_CUSTOMPATH}/cart/${id}`;
        const vm = this;
        console.log(api);
        vm.isLoading = true;
        this.$http.delete(api).then(response => {
            vm.isLoading = false;
            console.log(response.data);
            vm.getcart();
        });
    },
  },
  computed:{
      filterproduct(){
          if(this.productcategory == 'all'){
              return this.products;
          }
          else if(this.productcategory == 'milc'){
              const milcproduct =[];
              this.products.forEach(function(item){
                  if(item.category == '微單眼'){
                      milcproduct.push(item);
                  }
              })
              return milcproduct;
          }
          else if(this.productcategory == 'slrlike'){
              const slrlikeproduct =[];
              this.products.forEach(function(item){
                  if(item.category == '類單眼'){
                      slrlikeproduct.push(item);
                  }
              })
              return slrlikeproduct;
          }
          else if(this.productcategory == 'dslr'){
              const dslrproduct =[];
              this.products.forEach(function(item){
                  if(item.category == '專業單眼'){
                      dslrproduct.push(item);
                  }
              })
              return dslrproduct;
          }
      }
  },
  created() {
    this.getProducts();
    this.getcart();
  }
};
</script>