<template>
    <div>
        <div class="container">
            <div class="row justify-content-center my-3">
                <h2>購 物 車 清 單</h2>
            </div>
            <table class="table">
                <thead>
                    <th></th>
                    <th>品名</th>
                    <th>數量</th>
                    <th>單價</th>
                    <th>總計</th>
                </thead>
                <tbody>
                    <tr v-for="item in carts.carts" :key="item.id">
                    <td class="align-middle">
                        <button type="button" class="btn btn-outline-danger btn-sm" @click="deletecart(item.id)">
                        <i class="far fa-trash-alt"></i>
                        </button>
                    </td>
                    <td class="align-middle">
                        {{ item.product.title }}
                        <!-- <div class="text-success" v-if="item.coupon">
                        已套用優惠券
                        </div> -->
                    </td>
                    <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                    <td class="align-middle">{{ item.product.price }}</td>
                    <td class="align-middle text-right">{{ item.final_total}}</td>
                    </tr>
                </tbody>
                <tfoot>
                    <tr>
                    <td colspan="4" class="text-right">總計</td>
                    <td class="text-right">{{ carts.total}}</td>
                    </tr>
                    <tr>
                    <td colspan="4" class="text-right text-success">折扣價</td>
                    <td class="text-right text-success">{{ carts.final_total }}</td>
                    </tr>
                </tfoot>
            </table>
            <div class="input-group mb-3 input-group-sm">
                <input type="text" class="form-control" placeholder="請輸入優惠碼">
                <div class="input-group-append">
                    <button class="btn btn-outline-secondary" type="button">
                    套用優惠碼
                    </button>
                </div>
            </div>
        </div>

        <div class="my-5 row justify-content-center">
            <form class="col-md-6" @submit.prevent="createorder ">
                <div class="form-group">
                <label for="useremail">Email</label>
                <input v-validate="'required|email'" type="email" class="form-control" name="email" id="useremail"
                    v-model="form.user.email" placeholder="請輸入 Email">
                <span class="text-danger" v-if="errors.has('email')">{{ errors.first('email') }}</span>
                </div>
            
                <div class="form-group">
                <label for="username">收件人姓名</label>
                <input v-validate="'required'" type="text" class="form-control" name="name" id="username"
                    v-model="form.user.name" placeholder="輸入姓名">
                <span class="text-danger" v-if="errors.has('name')">姓名必須輸入</span>
                </div>
            
                <div class="form-group">
                <label for="usertel">收件人電話</label>
                <input v-validate="'required'" type="tel" class="form-control" name="tel" id="usertel" v-model="form.user.tel" placeholder="請輸入電話">
                <span class="text-danger" v-if="errors.has('tel')">電話欄位不得留空</span>
                </div>
            
                <div class="form-group">
                <label for="useraddress">收件人地址</label>
                <input v-validate="'required'" type="address" class="form-control" name="address" id="useraddress" v-model="form.user.address"
                    placeholder="請輸入地址">
                <span class="text-danger" v-if="errors.has('address')">地址欄位不得留空</span>
                </div>
            
                <div class="form-group">
                <label for="useraddress">留言</label>
                <textarea name="" id="" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
                </div>
                <div class="text-right">
                <button class="btn btn-danger">送出訂單</button>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
    import $ from 'jquery';
    
    export default{
        data(){
            return{
                carts:{},
                isLoading: false,
                fullPage: true,
                form:{
                    user:{
                    name:'',
                    email:'',
                    tel:'',
                    address:'',
                    },
                    message:'',
                },
            }
        },
        methods:{
             getcart(){
                const api = `${process.env.VUE_APP_APIMAIN}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
                const vm = this;
                console.log(api);
                this.$http.get(api).then(response => {
                    vm.carts = response.data.data;
                    // vm.finaltotal = response.data.final_total;
                    // vm.total = response.data.total;
                    console.log(response.data);
                });
            },
            deletecart(id){
                const api = `${process.env.VUE_APP_APIMAIN}/api/${process.env.VUE_APP_CUSTOMPATH}/cart/${id}`;
                const vm = this;
                console.log(api);
                vm.isLoading = true;
                this.$http.delete(api).then(response => {
                    vm.isLoading = false;
                    console.log(response.data);
                    vm.getcart();
                });
            },
            createorder(){
                const api = `${process.env.VUE_APP_APIMAIN}/api/${process.env.VUE_APP_CUSTOMPATH}/order`;
                const vm = this;
                const order = vm.form;
                console.log(api);
                vm.isLoading = true;
                this.$validator.validate().then((result) => {
                if (result) {
                    this.$http.post(api,{data:order}).then(response => {
                    console.log(response.data);
                    if(response.data.success){
                        vm.$router.push(`/customercheckout/${response.data.orderId}`);
                    }
                    vm.isLoading = false
                });
                }
                else{
                    console.log('表單不完整');
                    vm.isLoading = false
                    return;
                }
            });
            },
        },
        created(){
            this.getcart();
        }
    }
</script>
