<template>
    <div class="fixed-bottom container-fluid mb-3">
        <loading :active.sync="isLoading"></loading>
        <div class="row justify-content-end">
            <div class="dropdown">
                  <button type="button" class="btn btn-sm btn-cart" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" @click.prevent="getshoppingdata">
                    <i class="fas fa-shopping-cart fa-2x"></i>
                </button>
                <div class="dropdown-menu dropdown-custom">
                    <div class="container">
                        <div class="row">
                            <div class="col-12">
                                <h6>已購買商品</h6>
                                <table>
                                    <tbody>
                                        <tr v-for="item in carts.carts" :key="item.id">
                                            <td class="align-middle" width="10%"><a class="btn"><i class="fas fa-trash" onclick=" event.stopPropagation();" @click.prevent="deletecart(item.id)"></i><i class="fas fa-spinner fa-spin" v-if=" status.loadingitem === item.id"></i></a></td>
                                            <td class="align-middle" width="40%">{{ item.product.title }}</td>
                                            <td class="align-middle" width="20%">{{ item.qty }}/{{ item.product.unit }}</td>
                                            <td class="align-middle" width="30%">{{ item.product.price | currency  }}</td>
                                        </tr>
                                    </tbody>    
                                </table>
                            </div>
                        </div>
                        <!-- <button class="btn btn-primary btn-block" href="">點擊此處結帳</button> -->
                        <router-link to="/shoppingcartpage" class="btn btn-primary btn-block">點擊此處結帳</router-link>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import $ from 'jquery';

    //onclick=" event.stopPropagation();" 點擊按鈕後防止dropdown closing

    export default{
        data(){
            return{
                carts:{},
                isLoading:false,
                status: {
                    loadingitem:'',
                },
            };
        },
        methods:{
            getcart(){
                const api = `${process.env.VUE_APP_APIMAIN}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
                const vm = this;
                this.$http.get(api).then(response => {
                    vm.carts = response.data.data;
                    // vm.finaltotal = response.data.final_total;
                    // vm.total = response.data.total;
                    console.log(response.data);
                });
            },
            getshoppingdata(){
                this.getcart();
            },
            deletecart(id){
                const api = `${process.env.VUE_APP_APIMAIN}/api/${process.env.VUE_APP_CUSTOMPATH}/cart/${id}`;
                const vm = this;
                // console.log(api);
                vm.status.loadingitem = id
                this.$http.delete(api).then(response => {
                    vm.status.loadingitem ='';
                    console.log(response.data);
                    vm.getcart();
                });
            },
        },
        created(){
            this.getcart();
        }
    }
</script>
