<template>
  <div>
    <a href="#" @click.prevent="signout">登出</a>
  </div>
</template>

<script>
export default {
  name: 'signout',
  methods:{
    signout(){
      const api = `${process.env.VUE_APP_APIMAIN}/logout`;
            console.log(api);
            const vm = this;
            this.$http.post(api).then((response) => {
            console.log(response.data)
            if(response.data.success){
                vm.$router.push('/login');
                //若登入成功則轉至首頁
            }
            });
    }
  },
}
</script>

