<template>
    <div>
        <!----loading效果---->
        <loading :active.sync="isLoading"
        :is-full-page="fullPage">
        </loading>

        <div class="text-right">
            <button class="btn btn-primary mt-2" @click.prevent="openModal(true)">建立新產品</button>
        </div>

        <!--Modal內容-->
        <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content border-0">
                <div class="modal-header bg-dark text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span>新增產品</span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div class="row">
                    <div class="col-sm-4">
                        <div class="form-group">
                        <label for="image">輸入圖片網址</label>
                        <input type="text" class="form-control" id="image"
                            placeholder="請輸入圖片連結" v-model="tempproduct.imageUrl">
                        </div>
                        <div class="form-group">
                        <label for="customFile">或 上傳圖片
                            <i class="fas fa-spinner fa-spin" v-if="status.fileuploading"></i>
                        </label>
                        <input type="file" id="customFile" class="form-control"
                            ref="files" @change="uploadfile">
                        </div>
                        <img :src="tempproduct.imageUrl"
                        class="img-fluid" alt="">
                    </div>
                    <div class="col-sm-8">
                        <div class="form-group">
                        <label for="title">標題</label>
                        <input type="text" class="form-control" id="title"
                            placeholder="請輸入標題" v-model="tempproduct.title">
                        </div>

                        <div class="form-row">
                        <div class="form-group col-md-6">
                            <label for="category">分類</label>
                            <input type="text" class="form-control" id="category"
                            placeholder="請輸入分類" v-model="tempproduct.category">
                        </div>
                        <div class="form-group col-md-6">
                            <label for="price">單位</label>
                            <input type="unit" class="form-control" id="unit"
                            placeholder="請輸入單位" v-model="tempproduct.unit">
                        </div>
                        </div>

                        <div class="form-row">
                        <div class="form-group col-md-6">
                        <label for="origin_price">原價</label>
                            <input type="number" class="form-control" id="origin_price"
                            placeholder="請輸入原價" v-model="tempproduct.origin_price">
                        </div>
                        <div class="form-group col-md-6">
                            <label for="price">售價</label>
                            <input type="number" class="form-control" id="price"
                            placeholder="請輸入售價" v-model="tempproduct.price">
                        </div>
                        </div>
                        <hr>
                        <div class="form-group">
                        <label for="description">產品描述</label>
                        <textarea type="text" class="form-control" id="description"
                            placeholder="請輸入產品描述" v-model="tempproduct.description"></textarea>
                        </div>
                        <div class="form-group">
                        <label for="content">說明內容</label>
                        <textarea type="text" class="form-control" id="content"
                            placeholder="請輸入產品說明內容" v-model="tempproduct.content"></textarea>
                        </div>
                        <div class="form-group">
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox"
                            id="is_enabled" v-model="tempproduct.is_enabled" :true-value="1" :false-value="0">
                            <label class="form-check-label" for="is_enabled">
                            是否啟用
                            </label>
                        </div>
                        </div>
                    </div>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
                    <button type="button" class="btn btn-primary" @click.prevent="updateproduct">確認</button>
                </div>
                </div>
            </div>
        </div>

        <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
            aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content border-0">
                <div class="modal-header bg-danger text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span>刪除產品</span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    是否刪除 <strong class="text-danger">{{ delproduct.title }}</strong> 商品(刪除後將無法恢復)。
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
                    <button type="button" class="btn btn-danger" @click="deleteproduct()">確認刪除</button>
                </div>
                </div>
            </div>
        </div>

        <table class="table mt-4">
            <thead>
                <th width="10%">分類</th>
                <th>產品名稱</th>
                <th width="15%">原價</th>
                <th width="15%">售價</th>
                <th width="10%">是否啟用</th>
                <th width="10%">編輯</th>
            </thead>
            <tbody>
                <tr v-for="item in products" :key="item.id">
                    <td>{{ item.category }}</td>
                    <td>{{ item.title }}</td>
                    <td class="text-right">{{ item.origin_price | currency}}</td>
                    <td class="text-right">{{ item.price | currency }}</td>
                    <td>
                        <span v-if="item.is_enabled" class="text-success">已啟用</span>
                        <span v-else class="text-warning">未啟用</span>
                    </td>
                    <td>
                    <button class="btn btn-outline-primary" @click="openModal(false,item)">編輯</button>
                    <button class="btn btn-outline-danger" @click="opendelModal(item)">刪除</button>
                    </td>
                </tr>
            </tbody>
        </table>

        <nav aria-label="Page navigation example">
            <ul class="pagination">
                <li class="page-item" :class="{'disabled' : !pagination.has_pre}">
                <a class="page-link" href="#" aria-label="Previous" @click.prevent="getProducts(pagination.current_page - 1)">
                    <span aria-hidden="true">&laquo;</span>
                    <span class="sr-only">Previous</span>
                </a>
                </li>
                <li class="page-item" v-for="page in  pagination.total_pages" :key="page" :class="{'active' : pagination.current_page === page}">
                    <a class="page-link" href="#" @click.prevent="getProducts(page)">{{ page }}</a>
                    </li>
                <li class="page-item" :class="{'disabled' : !pagination.has_next}">
                <a class="page-link" href="#" aria-label="Next" @click.prevent="getProducts(pagination.current_page + 1)">
                    <span aria-hidden="true">&raquo;</span>
                    <span class="sr-only">Next</span>
                </a>
                </li>
            </ul>
        </nav>
    </div>
</template>

<script>
import $ from 'jquery';

    export default{
        data(){
            return{
                products:[],
                tempproduct:{},
                delproduct:{},
                isnew:false,
                isLoading: false,
                fullPage: true,
                status:{
                    fileuploading:false,
                },
                pagination:{}
            };
        },
        methods:{
            ///Page => es6方法預設值
            getProducts(page = 1){
                const api = `${process.env.VUE_APP_APIMAIN}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/products?page=${page}`;
                const vm = this;
                console.log(api);
                vm.isLoading = true;
                this.$http.get(api).then((response) => {
                console.log(response.data)
                vm.isLoading = false;
                vm.products = response.data.products;
                vm.pagination = response.data.pagination;
                });
            },
            ///可傳入參數進入openModal ,在動作開始之前
            openModal(isnew , item){
            $('#productModal').modal('show');
            if(isnew){
                this.tempproduct ={};
                this.isnew = true;
            }
            else{
                ///Object assign = ES6的方法，將item傳入一個新物件。    
                this.tempproduct = Object.assign({},item);
                this.isnew = false;
            }
            },
            opendelModal(item){
            $('#delProductModal').modal('show');
            this.delproduct = Object.assign({},item);
            },
            updateproduct(){
                let api = `${process.env.VUE_APP_APIMAIN}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product`;
                let httpmethod = 'post';
                const vm = this;
                if(!vm.isnew){
                    api = `${process.env.VUE_APP_APIMAIN}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${vm.tempproduct.id}`;
                    console.log(api);
                    httpmethod = 'put';
                }
                console.log(api);
                this.$http[httpmethod](api,{data:vm.tempproduct}).then((response) => {
                console.log(response.data)
                if(response.data.success){
                    $('#productModal').modal('hide');
                    vm.getProducts();
                    console.log('新增成功>0<');
                }
                else{
                    $('#productModal').modal('hide');
                    vm.getProducts();
                    console.log('新增失敗');
                }
                });
            },
            deleteproduct(){
                const vm = this;
                    const deapi = `${process.env.VUE_APP_APIMAIN}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${vm.delproduct.id}`;
                    console.log(deapi);
                    this.$http.delete(deapi,{data:vm.delproduct}).then((response) => {
                    console.log(response.data);
                    $('#delProductModal').modal('hide');
                    vm.getProducts();
                });
            },
            uploadfile(){
                console.log(this);
                const uploadedfile = this.$refs.files.files[0];
                const vm = this;
                ////模擬傳統表單送出的方式:FormData()
                const formdata = new FormData();
                formdata.append('file-to-upload',uploadedfile);
                ///定義上傳路徑(api)
                const url = `${process.env.VUE_APP_APIMAIN}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/upload`;
                //請注意formdata中要依據規格修改內容:如此處需要修改content-type
                vm.status.fileuploading = true;
                this.$http.post(url,formdata,{
                    headers:{
                        'Content-Type':'multipart/form-data',
                    }
                }).then((response) =>{
                    console.log(response.data)
                    if(response.data.success){
                        // vm.tempproduct.imageUrl = response.data.imageUrl;
                        // console.log(vm.tempproduct);

                        //$set()使用方法:(寫入的物件,寫入的欄位名稱,要寫入的欄位)
                        vm.$set(vm.tempproduct,'imageUrl',response.data.imageUrl);
                    }
                    else{
                        this.$bus.$emit('message:push',response.data.message,'danger');
                    }
                    vm.status.fileuploading = false;
                });
            }
        },
        created() {
            this.getProducts();
            },
    }
</script>